<template>
    <div id="app">
        <div style="width: 640px; text-align: left; padding-top: 20px;">
            <img src="./assets/logo-migu.png" alt="logo" width="100">
            <h3>
                <span>Bullet Comment Demonstration project</span>
            </h3>
        </div>

        <div class="danmu-container" ref="container"></div>
        <video autoplay controls poster="http://g.asyncoder.com/poster.png" id='video_v' ref="video">
            <source :src="video_src" type="video/mp4" id="v_source">
            <!--<track kind="subtiles" src="danmaku.vdt">https://g.asyncoder.com/sheep.mp4-->
        </video>
        <div class="tool">
            <input type="color" v-model="color">
            <select name="mode" id="messageMode" v-model="mode">
                <option value="scroll">scroll</option>
                <option value="top">top</option>
                <option value="bottom">bottom</option>
            </select>
            <select name="size" id="fontSize" v-model="size">
                <option value=16 >16px</option>
                <option value=18 >18px</option>
                <option value=20 >20px</option>
                <option value=22 >22px</option>
                <option value=24 >24px</option>
                <option value=26 >26px</option>
                <option value=28 >28px</option>
            </select>
            <input type="text" class="message-box" placeholder="Please enter the content of the barrage" v-model="message">
            <button @click="sendComment">send</button>
        </div>
        <div class="video_source">
            <span>切换视频源：</span>
            <select name="videoSource" id="videoSource" v-model="source_idx" @change="changeVideo">
                <option value=1>羊驼</option>
                <option value=2>全世界说咪咕</option>
                <option value=3>一个充满梦想的互联网内容乐园</option>
                <option value=4>同一个世界，同一声咪咕</option>
                <option value=5>说好不哭</option>
                <option value=6>抖音</option>
            </select>
        </div>
    </div>
</template>

<script>
/* eslint-disable indent,no-trailing-spaces */

    import { WebVTT } from 'vtt.js'
    import Danmaku from 'danmu.js'
    import { DANMAKU_DATA } from './assets/danmakuData'

    export default {
      name: 'app',
      data() {
        return {
          message: '',
          mode: 'scroll',
          color: '#fff316',
          size: 20,
          id: 0,
          isReady: false,
          source_idx: 1,
          video_src: 'https://storage.googleapis.com/media-session/caminandes/short.mp4',
          video_source_list: [
            'https://storage.googleapis.com/media-session/caminandes/short.mp4',
            '//storage.googleapis.com/media-session/caminandes/short.mp4',
            '//storage.googleapis.com/media-session/sintel/trailer.mp4',
            'https://cdn.migu.cn/video/migu_18.mp4',
            'https://cdn.migu.cn/video/migu_2.mp4',
            'https://cdn.migu.cn/video/migu_4.mp4',
            'http://vod.migu.cn/O8Ddul1PSXkINjoDXrlKGuAA4dL9QkJ/AIZf0hbunWIbKUat5q756MmpIO3UdGYnFgF1J/rVmlLXcvVbfx9qh2G1f0w+A2p7gCqTDUTgnVt4cjCaT97l188eIO58HySCF8Oef2CimtxLRmxEasfdR59RH50oHy6WtAVdICAUmVaHKw0zP2nbz9XMJK+NThGf/600547Y0003.mp4?msisdn=2a257d4c-1ee0-4ad8-8081-b1650c26390a&spid=600906&sid=53841184524100&timestamp=20190923204455&encrypt=3e966a51c420c245b3837e66dd812561&k=2f1952da853474f0&t=1569249895034&ec=1&FN=%E8%AF%B4%E5%A5%BD%E4%B8%8D%E5%93%AD',
            '//s3.pstatp.com/aweme/resource/web/static/image/index/tvc-v3_4138031.mp4'
          ]
        }
      },
      watch: {
        video_src(newV, oldV) {
          if (newV !== oldV) {
            this.initVideo()
            this.$refs.video.load()
            this.danmaku.start()
            this.$refs.video.play()
          }
        }
      },
      mounted() {
        this.isReady = false
        this.loadTracks().then(() => {
          // const arrays = this.parseVDT(vdt)
          const list = this.formatData(DANMAKU_DATA)
          const danmaku = this.createDanmaku(list)
          this.danmaku = danmaku
          // console.log('list', list)
          this.$refs.video.addEventListener(
            'play',
            () => {
              if (this.isReady) {
                this.danmaku.play()
                return
              }
              this.isReady = true
              this.danmaku.start()
            },
            false
          )

          this.$refs.video.addEventListener(
            'pause',
            () => {
              this.danmaku.pause()
            },
            false
          )
        })
      },

      methods: {
        initVideo() {
          this.loadTracks().then(() => {
            // const arrays = this.parseVDT(vdt)
            const list = this.formatData(DANMAKU_DATA)
            const danmaku = this.createDanmaku(list)
            this.danmaku = danmaku
            // console.log('danmaku:', this.danmaku)
            this.$refs.video.addEventListener(
              'play',
              () => {
                if (this.isReady) {
                  this.danmaku.play()
                  return
                }
                this.isReady = true
                this.danmaku.start()
              },
              false
            )

            this.$refs.video.addEventListener(
              'pause',
              () => {
                this.danmaku.pause()
              },
              false
            )
          })
        },
        changeVideo() {
          // console.log('source_idx:', this.source_idx)
          this.video_src = this.video_source_list[this.source_idx - 1]
        },
        sendComment() {
          // 发送弹幕
          this.id = this.id + 1
          let comment = {
            duration: 5000,
            id: `new${this.id}`,
            txt: 'Barrage content',
            style: {
              color: '#ffffff',
              fontSize: '20px'
            },
            mode: 'top'
          }
          let mes = this.message.trim()
          if (mes !== '') {
            comment.txt = mes
            comment.mode = this.mode
            comment.style.color = this.color || '#ffffff'
            comment.style.fontSize = `${this.size}px` || '20px'
            this.danmaku.sendComment(comment)
            // this.message = ''
          } else {
            alert('Please enter the content!')
          }
        },
        formatData(data) {
          // console.log('fd:', data)
          let cues = data
          return cues.map((cue, index) => {
            const { startTime, text: txt, color, fontSize, mode, duration } = cue
            return {
              id: index,
              duration: duration,
              start: startTime,
              txt,
              prior: true,
              color: true,
              mode,
              style: {
                color,
                fontSize: `${fontSize}px`
              }
            }
          })
        },
        loadTracks() {
          // const url = document.getElementById('v_source').src
          const url = this.video_src
          // console.log('url:', url)
          return fetch(url, {
            headers: { 'content-type': 'video/mp4' }
          }).then(res => res)
        },

        parseVDT(vdt) {
          const parser = new WebVTT.Parser(window, WebVTT.StringDecoder())
          const cues = []
          parser.oncue = function(cue) {
            cues.push(cue)
          }
          parser.parse(vdt.trim())
          parser.flush()

          return cues.map((cue, index) => {
            const { startTime, endTime, text } = cue
            const { text: txt, color, fontSize, mode } = JSON.parse(text)
            return {
              id: index,
              duration: endTime * 1000 - startTime * 1000,
              start: startTime * 1000,
              txt,
              style: {
                color,
                fontSize: `${fontSize}px`
              },
              mode
            }
          })
        },

        createDanmaku(comments) {
          return new Danmaku({
            container: this.$refs.container,
            containerStyle: {
              zIndex: 100
            },
            prior: true,
            color: true,
            comments,
            player: this.$refs.video,
            defaultOff: true
          })
        }
      }
    }
</script>

